import React from 'react'
import Paper from '@mui/material/Paper';
import { AppContext } from '../components/context';

const Result = () => {
    const {formValues} = React.useContext(AppContext)

  return (
    <Paper style={{padding: 30}} elevation={2}>
      <h2> :</h2>
      <ul>
        <li>
            <b>:</b> {formValues.firstName}
        </li>
        <li>
            <b>:</b> {formValues.lastName}
        </li>
        <li>
            <b>:</b> {formValues.email}
        </li>
        <li>
            <b>:</b> {formValues.password}
        </li>
      </ul>
      <hr />
        <h2></h2>
        <ul>
        <li>
            <b>:</b> {formValues.city}
        </li>
        <li>
            <b>:</b> {formValues.street}
        </li>
        <li>
            <b> :</b> {formValues.appartment}
        </li>
        </ul>
    </Paper>
  )
}

export default Result
